.form-control {
    margin: 0.5rem 0;
}

.form-control label {
    font-weight: bold;
    display: block;
    margin-bottom: 0.5rem;
}

.form-control input {
    display: block;
    width: 100%;
    border: 1px solid #ccc;
    font: inherit;
    line-height: 1.5rem;
    padding: 0 0.25rem;
}

.form-control input:focus {
    outline: none;
    background: #fad0ec;
    border-color: #078b00;
}

.form-control.invaild input {
    background-color: rgb(238, 181, 181);
    border-color: red;
}

.form-control.invaild label {
    color: red;
}